<template>
    <div class="content-bg"  v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">协议流量</div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="protocolFlowDateBus2"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">传输层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.TUData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">传输层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.TUData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">应用层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.appData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">应用层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.appData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">协议长度综合统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.agreementData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">协议长度综合统计</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.agreementData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "protocolFlow2",
        data() {
            return {
                fullscreenLoading:false,
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //传输层协议长度统计
                TUData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'传输层\n协议',
                        yAxisName:'数据包长度/KB',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                TUData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //应用层协议长度统计
                appData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'应用层\n协议',
                        yAxisName:'数据包长度/KB',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                appData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //协议长度综合统计
                agreementData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'协议',
                        yAxisName:'数据包长度/KB',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                agreementData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('protocolFlowDateBus2',(arr)=>{
              let paramObj = {
                starttime:arr[0],
                endtime:arr[1]
              }
              this.fullscreenLoading = true;
              /*获取传输层协议长度统计*/
              this.getTUData(paramObj);
              /*获取应用层协议长度统计*/
              this.getAppData(paramObj);
              /*获取协议长度综合统计*/
              this.getAgreementData(paramObj);
            })
        },
        mounted(){},
        methods:{
            /*获取传输层协议长度统计*/
            getTUData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getTransportLength.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                             this.TUData.xAxisArr = xns1;
                             this.TUData.yAxisArr = yvs1;
                             this.TUData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取应用层协议长度统计*/
            getAppData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getApplicationLength.do',paramObj)
                        .then(res=>{
                          this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                             this.appData.xAxisArr = xns1;
                             this.appData.yAxisArr = yvs1;
                            this.appData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取协议长度综合统计*/
            getAgreementData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getMultipleLength.do',paramObj)
                        .then(res=>{
                          this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                                /*yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })*/
                                for(let j in res[i]){
                                    xns1.push(j);
                                    yvs1.push(res[i][j]);
                                    yvs2.push({
                                        name:j,
                                        value:res[i][j]
                                    })
                                }
                            }
                             this.agreementData.xAxisArr = xns1;
                             this.agreementData.yAxisArr = yvs1;
                            this.agreementData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
        },
        components:{
            vBasedate,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



